










	let _self, openid, session_key, pageOptions;
  const sign = require('../../common/sign.js');
	export default {
		onLoad(options) {
      sign.sign(this.apiServer);
			_self = this;
			pageOptions = options;

			uni.login({
				success: function(loginRes) {
					uni.request({
						url: _self.apiServer + 'member&m=codeToSession&code=' + loginRes.code,
						method: 'GET',
						data: {},
						success: res => {
							openid = res.data.openid;
							session_key = res.data.session_key;
							_self.autoGetUserInfo();
						},
						fail: () => {},
						complete: () => {}
					});
				}
			});

		},
		data() {
			return {};
		},
		methods: {
			autoGetUserInfo(e) {
				if (e) {
					console.log('用户信息：', e);
				}
        const sign = uni.getStorageSync('sign');
        console.log(sign, 'sign');
				uni.getUserInfo({
					success: (info) => {
						const {
							nickName,
							avatarUrl
						} = JSON.parse(info.rawData)
						uni.request({
							url: _self.apiServer + 'member&m=login',
							header: {
								'content-type': "application/x-www-form-urlencoded"
							},
							method: 'POST',
							data: {
								openid: openid,
								name: nickName,
								face: avatarUrl,
                sign: sign
							},
							success: (res) => {
								console.log('userdata:', res);
								res = res.data;
								if (res.status == 'ok') {
									uni.showToast({
										title: "登录成功"
									});
									uni.setStorageSync('SUID', res.data.u_id + '');
									uni.setStorageSync('SRAND', res.data.u_random + '');
									uni.setStorageSync('SNAME', res.data.u_name + '');
									uni.setStorageSync('SFACE', res.data.u_face + '');
									// 跳转
									if (pageOptions.backtype == 1) {
										uni.redirectTo({
											url: pageOptions.backpage
										});
									} else {
										uni.switchTab({
											url: pageOptions.backpage
										});
									}
								} else {
									uni.showToast({
										title: res.data
									});
								}
							},
							fail: () => {},
							complete: () => {}
						});
					},
					fail(e) {

						uni.showToast({
							title: '请点击按钮登录',
							icon: 'none',
							duration: 2000
						})

































































						console.log(e);
					}
				});
			},
			getUserinfo: function(e) {
				this.autoGetUserInfo(e)
			},
		}
	};
